import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../../Images/IconPack";
import TextField from "@material-ui/core/TextField/TextField";
import InputLabel from "@material-ui/core/InputLabel/InputLabel";
import {
    setNewValueForStoreItem,
    createItem,
    deleteItem
} from "../../../../redux/licenses/corporate/generate/generate.actions";
import Button from "@material-ui/core/Button";
import {FormattedMessage} from 'react-intl';


const CorporateGenerate = (props) => {


    const addHandler = () => {
        const reducerStore = props.generateData.store;
        const reducerDefData = props.generateData.defValues;
        let ticketID = +new Date();


        let newObject = {
            login: reducerStore[reducerStore.length - 1].login + 1,
            password: reducerDefData.password,
            nickname: reducerDefData.nickname,
            telegram: reducerDefData.telegram,
            comment: reducerDefData.comment,
            ticketID: ticketID,
        };

        props.createItem(newObject);
    }

    const getRowItem = (itemData) => {

        return (<div className="generateCard">
                <span className="color-grey">Login</span>
                <TextField
                    fullWidth
                    value={'CU' + itemData.login}
                    onChange={(e) => {
                        props.setNewValueForStoreItem('login', e.target.value, itemData.ticketID)
                    }}
                    variant="filled"
                    disabled
                />

                <span className="color-grey">Password</span>
                <TextField
                    fullWidth
                    value={itemData.password}
                    onChange={(e) => {
                        props.setNewValueForStoreItem('password', e.target.value, itemData.ticketID)
                    }}
                    variant="filled"
                />

                <span className="color-grey">Nickname</span>
                <TextField
                    fullWidth
                    value={itemData.nickname}
                    onChange={(e) => {
                        props.setNewValueForStoreItem('nickname', e.target.value, itemData.ticketID)
                    }}
                    variant="filled"
                />

                <span className="color-grey">Telegram</span>
                <TextField
                    fullWidth
                    value={itemData.telegram}
                    onChange={(e) => {
                        props.setNewValueForStoreItem('telegram', e.target.value, itemData.ticketID)
                    }}
                    variant="filled"
                />

                <span className="color-grey">Comment</span>
                <TextField
                    fullWidth
                    value={itemData.comment}
                    onChange={(e) => {
                        props.setNewValueForStoreItem('comment', e.target.value, itemData.ticketID)
                    }}
                    variant="filled"
                />

                <Button variant="contained" className="btn btn--red mb-15">
                    Exclude
                </Button>
            </div>
        );
    }


    return (
        <div className="ÑustomStyle">
            <div className="alertPanel alertPanel--mobile">
                <p className="color-white">Prepare accounts for their future owners. It is not necessary to write down
                    the login and password at this stage, you can easily do it later</p>
                <span className="btn-text color-blue">MORE</span>
            </div>

            <div className="generateItems__wrapper">
            {props.generateData.store.map(itemData => {
                return getRowItem(itemData);
            })}
            </div>

        </div>
    );
}

const mapStateToProps = (state) => ({
    generateData: state.corporateLicenseGenerate.generate,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setNewValueForStoreItem: (item, itemValue, id) => dispatch(setNewValueForStoreItem(item, itemValue, id)),
        createItem: (object) => dispatch(createItem(object)),
        deleteItem: (id) => dispatch(deleteItem(id)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(CorporateGenerate);
